.Segment-On {
    background-color: red;
    border: 1px solid rgb(196, 28, 28);
    border-radius: 50%;
}

.Segment-Off {
    background-color: transparent;
    border: 2px solid rgb(90, 23, 23);
    border-radius: 50%;
}

.Counter {
    width: 30px;
    height: 50px;
    position: relative;
    background-color: black;
}


